.pg-container.services {
  align-items: center;
}

.services h1 {
  align-self: flex-start;
}

/*OUR PROCESS START*/
.services-slider {
  display: flex;
  flex-wrap: wrap;
  gap: 2em;
  margin-top: 2em;
  margin-bottom: 6em;
}

.process-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  background-color: var(--black);
  padding: 1.8em 1.8em 2em 1.8em;
  box-shadow: 2px 4px 32px #5d26f65b;
  border-radius: 30px;
}

.services-slider img {
  width: 3em;
}

.services-slider h4 {
  font-size: 0.8em;
  margin-block-start: 1em;
  margin-block-end: 0.6em;
}

.services-slider p {
  margin-block-start: 0;
  font-size: 0.58em;
  line-height: 1.78;
  text-align: center;
  font-weight: 300;
  color: var(--light);
}

/*OUR PROCESS END*/

.services h2 {
  margin-top: 3em;
  font-size: 1.28em;
  font-weight: 900;
}

.services-blurb {
  text-align: center;
  line-height: 1.1;
  margin-top: 2em;
  margin-bottom: 3em;
}

.service-cards {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.8em;
  margin-bottom: 3em;
}

.row {
  display: flex;
  flex-direction: column;
  align-self: center;
}

.service-cards div {
  display: flex;
  gap: 0.8em;
}

.card img {
  width: 2.6em;
}

.card h4 {
  font-size: 0.56em;
  text-align: center;
  line-height: 1.4;
  margin-block-end: 0;
  font-weight: 900;
}

.services .circle-gradient {
  right: 12em;
  top: 4em;
  background: radial-gradient(
    12% 30% at 60% 45%,
    rgba(93, 38, 246, 0.288) 0%,
    rgba(92, 38, 246, 0) 100%
  );
  position: absolute;
  width: 55em;
  z-index: -1;
  height: 20em;
}

.services .card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: var(--black);
  width: 8em;
  height: 5em;
  border-radius: 30px;
  gap: 0;
  padding: 1.8em 1.2em;
  transition: all 0.6s ease-out;
  transform: rotateX(0deg) rotateY(0deg);
  perspective: 1000px;
  transform-style: preserve-3d;
}

.card p {
  font-size: 0.54em;
  line-height: 1.6;
  color: var(--light);
  text-align: center;
  margin-block-start: 0;
  margin-block-end: 0;
}

/* HOVER ANIMATION */

.card-wrapper {
  transition: all 0.6s ease-out;
  perspective: 1000px;
}

.card-wrapper:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.t_over {
  width: 33.333%;
  height: 33.333%;
  position: absolute;
  z-index: 1;
}

.t_over:nth-child(1) {
  left: 0;
  top: 0;
}

.t_over:nth-child(2) {
  left: 33.333%;
  top: 0;
}

.t_over:nth-child(3) {
  left: 66.666%;
  top: 0;
}

.t_over:nth-child(4) {
  left: 0;
  top: 33.333%;
}

.t_over:nth-child(5) {
  left: 33.333%;
  top: 33.333%;
}

.t_over:nth-child(6) {
  left: 66.666%;
  top: 33.333%;
}

.t_over:nth-child(7) {
  left: 0;
  top: 66.666%;
}

.t_over:nth-child(8) {
  left: 33.333%;
  top: 66.666%;
}

.t_over:nth-child(9) {
  left: 66.666%;
  top: 66.666%;
}

.t_over:nth-child(1):hover ~ .card {
  box-shadow: 3px 10px 20px #5d26f696;
  transform: rotateX(-20deg) rotateY(20deg);
}

.t_over:nth-child(2):hover ~ .card {
  box-shadow: 3px 10px 20px #5d26f6cc;
  transform: rotateX(-20deg) rotateY(0deg);
}

.t_over:nth-child(3):hover ~ .card {
  box-shadow: 3px 2px 20px #5d26f6cc;
  transform: rotateX(-20deg) rotateY(-20deg);
}

.t_over:nth-child(4):hover ~ .card {
  box-shadow: 3px 10px 20px #5d26f6cc;
  transform: rotateX(0deg) rotateY(20deg);
}

.t_over:nth-child(5):hover ~ .card {
  box-shadow: 3px 10px 20px #5d26f691;
  transform: rotateX(0deg) rotateY(0deg);
}

.t_over:nth-child(6):hover ~ .card {
  box-shadow: 3px 10px 20px #5d26f686;
  transform: rotateX(0deg) rotateY(-20deg);
}

.t_over:nth-child(7):hover ~ .card {
  box-shadow: 0px -10px 20px #5d26f66e;
  transform: rotateX(20deg) rotateY(20deg);
}

.t_over:nth-child(8):hover ~ .card {
  box-shadow: 3px -10px 20px #5d26f66e;
  transform: rotateX(20deg) rotateY(0deg);
}

.t_over:nth-child(9):hover ~ .card {
  box-shadow: 3px -10px 20px #5d26f657;
  transform: rotateX(20deg) rotateY(-20deg);
}
